import React from 'react';

import ShowFirstChildInfo from './showFirstChildInfo/showFirstChildInfo';
import ShowClickChildInfo from './showClickChildInfo/showClickChildInfo';

const InfoCurrentComponent = (props) => {

    let { key, currentClickChildInfo, isShowFirstChildInfo, showFirtItem } = props;

    return (
        <div className="nameInfo_current_component_childs" key={key}>
            {isShowFirstChildInfo === true ?
                < ShowFirstChildInfo
                    showFirtItem={showFirtItem}
                    key={key}
                />
                :
                < ShowClickChildInfo
                    key={key}
                    currentClickChildInfo={currentClickChildInfo}
                />
            }
        </div>
    )
}
export default InfoCurrentComponent;

