import React from 'react';
import { NavLink } from 'react-router-dom';

import { ProductConsumer } from '../../context';
import ShowCartProducts from './showCartProducts';
import DrawerToggleButton from '../SliderMenu/drawerToggleButton';

const Header = ({ drawerClickHandler }) => {
    return (
        <header>
            <div className="toolbar_navigation">
                <div className="toggleBtn_logo">
                    <div className="tollbar_toggle_button">
                        < DrawerToggleButton click={drawerClickHandler} />
                    </div>

                    <div className="toolbar_logo">
                        <span><img src={require('../../images/logo.png')} alt="" /></span>
                    </div>
                </div>

                <div className="toolbar_navigations_items  ">
                    <ul>
                        <li><NavLink to="/">Home</NavLink></li>
                        <li><NavLink to="/games" activeStyle={{ color: '#ffffff' }} >Games</NavLink></li>
                        <li><NavLink to="/computerHardWare" activeStyle={{ color: '#ffffff' }} >PC-Hardware</NavLink></li>
                        <li><NavLink to="/laptops" activeStyle={{ color: '#ffffff' }} >Laptops</NavLink></li>
                        <li><NavLink to="/contacts" activeStyle={{ color: '#ffffff' }} >Contacts</NavLink></li>

                    </ul>
                </div>
                <div className="cart">
                    <ProductConsumer>
                        {value => < ShowCartProducts count={value.countHeaderCart} />}
                    </ProductConsumer>
                </div>
            </div>
        </header>
    )
}
export default Header;


