import React, { Fragment } from 'react';

import { Link } from 'react-router-dom';
import { ProductConsumer } from '../../../../../../context';

function ShowClickChildInfo({ currentClickChildInfo, key }) {
    return (
        <Fragment>
            <h3>{currentClickChildInfo.itemOfModel_name}</h3>
            <div className="info_child" key={key}>
                <div key={key} className="nameInfo_current_component_childs_image">

                    <img src={currentClickChildInfo.picture} alt="" />
                </div>
                <div className="product_specifications">
                    <span>Specifications</span>
                    <h4>In progress...</h4>
                </div>
            </div>
            {<div className="price_and_buyBtn" key={key}>
                <h3>${currentClickChildInfo.price}</h3>

                <ProductConsumer>
                    {value => {
                        return (
                            <Link to="#" onClick={() => value.addToCart(currentClickChildInfo)} className="btn_buy"> Buy now</Link>
                        )
                    }}
                </ ProductConsumer >
            </div>}
        </Fragment>
    )
}
export default ShowClickChildInfo;