import React from 'react';

import { Link } from 'react-router-dom';
import FixedFormatPrice from '../../fixedFormatPrice';

function NoEmptyCart({ cart, orderTotal, removeProduct }) {
    return (
        <div className="background_image_cart">
            <br />
            <div className="res_tab">
                <table>
                    <tbody>
                        <tr>
                            <th>picture</th>
                            <th className="fixed_name_length">name</th>
                            <th>price</th>
                            <th>quantity</th>
                            <th>total price</th>
                        </tr>
                        {cart.map((product, key) => {
                            let a = product.price;
                            let b = product.countOrder;
                            let result = a * b;
                            orderTotal += result;
                            return (<tr key={product.id}>
                                <td><img src={product.picture} alt="" /></td>
                                <td><span>{product.itemOfModel_name}</span></td>
                                <td>${product.price}</td>
                                <td ><span>{product.countOrder}</span>
                                    <Link className="quantity_remove" to="/cart" onClick={() => removeProduct(product)}><i className="fas fa-trash-alt"></i></Link>
                                </td>
                                <td>{FixedFormatPrice.formatCurrency(result)}</td>
                            </tr>)
                        })}
                    </tbody>
                    <div className="order_total">
                        <span>Order total: {FixedFormatPrice.formatCurrency(orderTotal)}</span>
                    </div>
                </table>
            </div>
            <br />
        </div>
    )
}
export default NoEmptyCart;