import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import AllGames from './allGames';
import Filter from './filter';

import Data from '../../../data/dataGames';

class MainProducts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            productsGame: Data.properties,
            sort: '',
            startIndexGame: 0,
        }
        this.handlefilteredProducts = this.handlefilteredProducts.bind(this);

    }
    handlefilteredProducts(e) {
        this.setState({
            sort: e.target.value
        });

        this.setState(state => {
            if (state.sort !== '') {
                state.productsGame.sort((a, b) =>
                    (state.sort === 'lowest') ? (a.price > b.price ? 1 : -1) : (a.price < b.price ? 1 : -1))
            }
        })
    }
    show_1_button = () => {
        this.setState({
            startIndexGame: 0,
        })
    }
    show_2_button = (e) => {
        this.setState({
            startIndexGame: 8,
        })
    }
    show_3_button = () => {
        this.setState({
            startIndexGame: 16,
        })
    }

    render() {
        let { productsGame, currenGameIndex, startIndexGame } = this.state;
        return (
            <div className="background_Games">
                < Filter
                    countProductsGame={productsGame}
                    handlefilteredProducts={this.handlefilteredProducts}
                />

                < AllGames
                    productsGame={productsGame}
                    currenGameIndex={currenGameIndex}
                    startIndexGame={startIndexGame}
                    url={this.props.match.url}
                />
               <br/>
                
                <div className="buttons_next_products">
                    <ul>
                        <li><NavLink to="/games"  onClick={this.show_1_button} >1</NavLink></li>
                        <li><NavLink to="/games"  onClick={this.show_2_button} >2</NavLink></li>
                        <li><NavLink to="/games"  onClick={this.show_3_button} >3</NavLink></li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default MainProducts;