import React from 'react';
import { NavLink } from 'react-router-dom';

function ShowListChilds({key, item, handleCurrentClickChild}) {
    return (
        <div className="name_child" key={key}>
            <NavLink to={'#'} onClick={(e) =>
                handleCurrentClickChild(item, key)}>{item.itemOfModel_name}</NavLink>
        </div> 
    )
}
export default ShowListChilds;