import React from 'react';
import { Link } from 'react-router-dom';

import center_image from '../../../images/home_images/center_image.PNG';
import laptops from '../../../images/home_images/laptops.PNG';
import desktops from '../../../images/home_images/desktops.PNG';
import displays from '../../../images/home_images/displays.PNG';
import vr from '../../../images/home_images/VR.PNG';
import configure_pc from '../../../images/home_images/configure_Your PC.PNG';

const Main = () => {
    return (
        <div className="home">
            <div className="center_image_home">
                <img src={center_image} alt="" />
            </div>
            <div className="links_products_home">
                <div className="link_item_product">
                    <Link to="/laptops"> <img src={laptops} alt="" /></Link>
                </div>
                <div className="link_item_product">
                    <Link to="/computerHardWare"> <img src={desktops} alt="" /></Link>
                </div>
                <div className="link_item_product">
                    <Link to="/computerHardWare"> <img src={displays} alt="" /></Link>
                </div>
                <div className="link_item_product">
                    <Link to="/computerHardWare"> <img src={vr} alt="" /></Link>
                </div>
                <div className="link_item_product">
                    <Link to="/computerHardWare"> <img src={configure_pc} alt="" /></Link>
                </div>
            </div>
        </div>
    )
}

export default Main;