import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

class ShowCartProducts extends Component {
    // state = {
    //     saveLocalLength: 0
    // }
    render(){
        return (
                <li><NavLink to="/cart" onClick={this.props.clickCloseSlideMenu}
                activeStyle={{ color: '#FFFFFF' }} >  My Cart<i className="fas fa-cart-plus"></i></NavLink> ( <span className="count_products">{this.props.count}</span> )</li>
        )
    }

}
export default ShowCartProducts;
